<template>
  <el-container>
    <el-header>
      <el-row>
        <el-col :span="6">
          <el-input v-model="param" placeholder="搜索品名或者货号" clearable>
            <el-button slot="append" icon="el-icon-search">搜索</el-button>
          </el-input>
        </el-col>
        <el-col :span="6" :offset="12">
          <el-button type="primary">新建</el-button>
          <el-button type="primary" @click="editGoods">编辑</el-button>
          <el-button type="primary" @click="deleteGoods">删除</el-button>
        </el-col>
      </el-row>
    </el-header>
    <el-main>
      <el-row>
        <el-col :span="18">
          <el-table
            :data="tableData"
            border
            style="width: 100%"
            highlight-current-row
            @current-change="handleSelectionChange">
            <el-table-column
              prop="goodsNo"
              label="货号"
              width="180">
            </el-table-column>
            <el-table-column
              prop="goodsName"
              label="品名"
              width="180">
            </el-table-column>
            <el-table-column
              prop="note"
              label="备注">
            </el-table-column>
          </el-table>
          <div class="block">
            <el-pagination
              background
              layout="prev, pager, next,jumper"
              :page-count="totalPage"
            @current-change="toPage"

            >
            </el-pagination>
          </div>
        </el-col>
        <el-col :span="6">
          <el-container>
            <el-header height="400px">
              <template v-show="!currentRow.goodsPic===null">
                <img :src="currentRow.goodsPic"/>
              </template>
            </el-header>
            <el-main>
              供应商展示
              {{currentRow.address}}
            </el-main>
          </el-container>
        </el-col>
      </el-row>
    </el-main>

  </el-container>
</template>

<script>
import _conf from '@/global.js'
import axios from 'axios'
export default {
  name: 'Goods',
  data () {
    return {
      totalPage: 1,
      param: '',
      tableData: [],
      currentRow: {
        date: '',
        name: '',
        address: ''
      }

    }
  },
  computed: {
    currentIndex () {
      return this.tableData.findIndex(value => value === this.currentRow)[0]
    }
  },
  created () {
    this.currentRow = null
    this.toPage(1)
  },
  methods: {
    toPage (page) {
      let self = this
      axios.get(_conf.API + '/goods/toPage/' + page)
        .then(result => {
          self.tableData = result.data.list
          self.totalPage = result.data.totalPage
        })
    },
    handleSelectionChange (val) {
      this.currentRow = val
    },
    editGoods () {
      console.log(_conf.API)
    },
    checkChoose () {
      console.log(this.currentRow)
      if (this.currentRow == null) {
        this.$alert('你必须先选择一行', '提示')
        return false
      } else {
        return true
      }
    },
    deleteGoods () {
      var self = this
      if (this.checkChoose()) {
        this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          self.tableData.splice(self.currentIndex, 1)
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
      }
    }
  }
}
</script>

<style scoped>

</style>



