<template>
  <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
    <el-menu-item v-for="(item,index) in items"
                  v-bind:key="index"
                  :index="index">
      <router-link :to="item.href">{{item.name}}</router-link>
    </el-menu-item>
  </el-menu>
</template>

<script>
export default {
  name: 'CommonNav',
  data () {
    return {
      items: [
        {id: 1, href: '/goods', name: '产品档案'},
        {id: 2, href: '/goodsAndSupplier', name: '产品链接'},
        {id: 3, href: '/supplier', name: '供应商档案'},
        {id: 4, href: '/customer', name: '客户管理'},
        {id: 5, href: '/#', name: '新建订单'},
        {id: 6, href: '/#', name: '订单操作'},
        {id: 7, href: '/#', name: '订单查询'}
      ]
    }
  }
}
</script>

<style scoped>

</style>



