import Vue from 'vue'
import Router from 'vue-router'
import Goods from '@/components/views/Goods'
import GoodsAndSupplier from '@/components/views/GoodsAndSupplier'
Vue.use(Router)

export default new Router({
  routes: [
    {path: '/', component: Goods},
    {path: '/goods', component: Goods},
    {path: '/goodsAndSupplier', component: GoodsAndSupplier}
  ]
})



